import React, {useEffect, useState} from 'react'
import {DataView} from "primereact/dataview";
import copak from "../images/copak.png"
import {Link} from "react-router-dom";

interface Project {
    name: string;
    description: string;
    image: string;
}

export const Proyectos = () => {
    const [projects, setProjects] = useState<Project[]>([]);

    const proj = [
        {
            name: "Copa K",
            description: "la app Copa K lleva el, " +
                "seguimiento y control del torneo de futbol empresarial, " +
                "mostrando la puntuación y partidos",
            image: "src/images/copak.png"
        }
    ]

    useEffect(() => {
        setProjects(proj);
    }, [proj]);

    const gridItem = (project: Project) => {
        return (
            <div className="col-12 sm:col-6 lg:col-12 xl:col-4 p-2">
                <div className="p-2 border-1 surface-border surface-card border-round">
                    <div className="flex flex-column align-items-center gap-3 py-5">
                        <img className="shadow-2 border-round w-full"
                             src={copak}
                             alt={project.name}/>
                        <div className="text-2xl font-bold">{project.name}</div>
                        <div>
                            {project.description}
                        </div>
                        <div>
                            <Link to={"https://dorianrony.github.io/copak/#/copak/"} target="_blank"> App  </Link>
                            -
                            <Link to={"https://github.com/DorianRony/personalpage"} target="_blank"> Code </Link>
                        </div>
                    </div>
                </div>
            </div>
        );
    };
    return (
        <div className="card">
            <DataView value={projects} itemTemplate={gridItem}/>
        </div>
    )
}
